:root {
  --dark-background: #060610;
  --menu-background: rgba(25, 25, 25, 0.5);
  --menu-mobile-background: rgba(25, 25, 25);
  --white-background: rgba(255, 255, 255, 0.04);
  --svg-background: rgba(244, 36, 123, 0.2);
  --pink-blur: #ed1871;
  --light-pink: #e93580;
  --btn-pink-gradient: linear-gradient(
    179.34deg,
    #d2008c -47.95%,
    #e6007e 109.59%
  );
  --tg-color: #61cfff;
  --gray-line: rgba(255, 255, 255, 0.5);
  --table-background: #12121c;
  --table-first-background: #0b0b15;
  --article-background: #6e41f3;
  --blockquote-background: rgba(70, 66, 243, 0.2);
  --blockquote-border: #4642f3;
  --tg-border: rgba(118, 246, 255, 0.08);
  --btn-border: rgba(255, 255, 255, 0.1);
  --btn-box-shadow-s: rgba(237, 24, 113, 0.4);
  --btn-box-shadow-e: rgba(230, 0, 126, 0.2);
  --border-gray: rgba(255, 255, 255, 0.2);
  --gray-color: #949494;
  --light-transparent-color: rgba(255, 255, 255, 0.3);
  --mail-background: rgba(255, 255, 255, 0.08);
  --blue-border: rgba(58, 144, 255, 0.08);
  --light-color: rgba(255, 255, 255, 0.8);
  --arrow-background: rgba(255, 255, 255, 0.02);
  --arrow-border: rgba(255, 255, 255, 0.06);
  --arrow-box-shadow1: rgba(255, 255, 255, 0.25);
  --arrow-box-shadow2: rgba(0, 0, 0, 0.16);
  --font-inter: "Inter";
  --font-unbounded: "Unbounded";
}

body {
  background: var(--dark-background);
  font-size: 1.3125rem;
  color: var(--bs-white);
  min-height: 100vh;
  overflow-x: hidden;
  max-width: 100vw;

  & * {
    font-family: var(--font-inter);
  }
}

button {
  outline: none;
}

.container {
  max-width: 1782px;
  padding: 0 1.3125rem;
  position: relative;

  @media (max-width: 768px) {
    padding: 0 1rem;
  }

  @media (max-width: 1780px) {
    overflow: hidden;
  }
}

p {
  margin-bottom: 0;
  hyphens: auto;
}

a {
  color: inherit;
  text-decoration: none;

  &:hover {
    color: inherit;
  }
}

ul {
  list-style-type: none;
  margin-bottom: 0;
}

h2.fs-2 {
  font-weight: 600;
  position: relative;
  z-index: 0;

  @media (max-width: 992px) {
    font-size: 2rem !important;
  }

  @media (max-width: 550px) {
    font-size: 1.3125rem !important;
  }
}

.hidden {
  overflow: hidden;
}

.main {
  padding-top: 7rem;

  h1 {
    font-weight: 500;
    font-family: var(--font-unbounded);
  }
}

.default-background {
  background: var(--white-background);
  border: 2px solid var(--white-background);
  backdrop-filter: blur(36px);
}

.default-list {
  list-style-type: disc;
}

// circles

.circle-1 {
  position: absolute;
  top: -49vw;
  right: 27.25vw;
  width: 100vw;
  min-height: 100vw;
  border-radius: 50%;
  background: radial-gradient(
    90% 90% at -6% 29%,
    rgba(210, 0, 140, 0) 0%,
    rgba(210, 0, 140, 0.3) 35%,
    #d20043 100%
  );
  transform: rotate(180deg);
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  mix-blend-mode: color-dodge;
  opacity: 0.5;

  &:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: 6%;
    left: -20%;
    background: radial-gradient(
      circle at center,
      rgba(255, 255, 255, 0.75) 0%,
      rgba(255, 255, 255, 0.7) 15%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0.08) 60%,
      rgba(255, 255, 255, 0) 66%
    );
    width: 55%;
    height: 55%;
    transform-origin: center;
    // animation: moveAround 5s linear infinite;
  }

  @media (max-width: 992px) {
    top: -8%;
    right: 38%;
    width: 100vh;
    min-height: 100vh;
  }
}

