.circle-2 {
  position: absolute;
  top: 10vw;
  left: 65.6vw;
  width: 69.2vw;
  height: 69.2vw;
  border-radius: 50%;
  background: radial-gradient(
    90% 90% at -6% 29%,
    rgba(210, 0, 140, 0) 0%,
    rgba(210, 0, 140, 0.3) 35%,
    #d20043 100%
  );
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  mix-blend-mode: color-dodge;
  opacity: 0.5;

  &:before {
    content: "";
    position: absolute;
    z-index: 0;
    top: -13%;
    left: -14%;
    background: radial-gradient(
      circle at center,
      rgba(255, 255, 255, 0.75) 0%,
      rgba(255, 255, 255, 0.7) 15%,
      rgba(255, 255, 255, 0.2) 50%,
      rgba(255, 255, 255, 0.08) 60%,
      rgba(255, 255, 255, 0) 66%
    );
    width: 55%;
    height: 55%;
  }

  @media (max-width: 992px) {
    top: 29%;
    left: 60%;
    width: 69.2vh;
    height: 69.2vh;
  }

  @media (max-width: 768px) {
    left: 50%;
  }
}

.main-blur1 {
  position: absolute;
  top: -24vw;
  left: -3.5vw;
  transform: rotate(20deg);
  width: 100vw;
  height: 64vw;
  opacity: 0.4;
  z-index: -1;
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(237, 24, 113, 1) 0%,
    rgba(237, 24, 113, 0.5) 39.5%,
    rgba(237, 24, 113, 0.3) 70.99%,
    rgba(237, 24, 113, 0) 100%
  );

  @media (max-width: 992px) {
    position: absolute;
    top: -46vw;
    left: -32.5vw;
    -webkit-transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    transform: rotate(20deg);
    width: 150vw;
    height: 106vh;
    opacity: 0.5;
  }

  @media (max-width: 768px) {
    width: 250vw;
    left: -68.5vw;
  }
}

.background-blur {
  pointer-events: none;
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(190, 0, 104, 0.55) 0%,
    rgba(139, 10, 81, 0.5) 39.5%,
    rgba(71, 4, 41, 0.3) 70.99%,
    rgba(26, 1, 14, 0) 100%
  );
}

.background-blue-blur {
  pointer-events: none;
  background: radial-gradient(
    ellipse at center,
    rgba(70, 66, 243, 0.3) 0%,
    rgba(53, 47, 171, 0.2) 40%,
    rgba(38, 30, 106, 0.02) 71%,
    rgba(18, 7, 21, 0) 80%
  );

  &.services {
    width: 100%;
    min-height: 140%;
    opacity: 0.55;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 768px) {
      width: 200%;
      height: 140%;
      opacity: 0.55;
      left: -50%;
      top: -10%;
    }
  }

  &.helpful {
    width: 100%;
    height: 140%;
    opacity: 0.5;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 768px) {
      opacity: 0.7;
      top: 20%;
      width: 200%;
    }
  }

  &.reviews {
    width: 120%;
    height: 120%;
    opacity: 0.5;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @media (max-width: 768px) {
      height: 160%;
    }
  }
}

.container h2 {
  @media (max-width: 550px) {
    font-size: 1.3125rem !important;
  }
}

.accordion-wrapper {
  grid-template-columns: repeat(12, 1fr);

  & > *:first-child {
    grid-column: 3 / 11;
  }

  @media (max-width: 1500px) {
    & > *:first-child {
      grid-column: 1 / 13;
    }
  }
}
