:root {
  --dark-background: #060610;
  --menu-background: rgba(25, 25, 25, 0.5);
  --menu-mobile-background: rgba(25, 25, 25);
  --white-background: rgba(255, 255, 255, 0.04);
  --svg-background: rgba(244, 36, 123, 0.2);
  --pink-blur: #ed1871;
  --light-pink: #e93580;
  --btn-pink-gradient: linear-gradient(
    179.34deg,
    #d2008c -47.95%,
    #e6007e 109.59%
  );
  --tg-color: #61cfff;
  --gray-line: rgba(255, 255, 255, 0.5);
  --table-background: #12121c;
  --table-first-background: #0b0b15;
  --article-background: #6e41f3;
  --blockquote-background: rgba(70, 66, 243, 0.2);
  --blockquote-border: #4642f3;
  --tg-border: rgba(118, 246, 255, 0.08);
  --btn-border: rgba(255, 255, 255, 0.1);
  --btn-box-shadow-s: rgba(237, 24, 113, 0.4);
  --btn-box-shadow-e: rgba(230, 0, 126, 0.2);
  --border-gray: rgba(255, 255, 255, 0.2);
  --gray-color: #949494;
  --light-transparent-color: rgba(255, 255, 255, 0.3);
  --mail-background: rgba(255, 255, 255, 0.08);
  --blue-border: rgba(58, 144, 255, 0.08);
  --light-color: rgba(255, 255, 255, 0.8);
  --arrow-background: rgba(255, 255, 255, 0.02);
  --arrow-border: rgba(255, 255, 255, 0.06);
  --arrow-box-shadow1: rgba(255, 255, 255, 0.25);
  --arrow-box-shadow2: rgba(0, 0, 0, 0.16);
  --font-inter: "Inter";
  --font-unbounded: "Unbounded";
}

body {
  background: var(--dark-background);
  font-size: 1.3125rem;
  color: var(--bs-white);
  min-height: 100vh;
  overflow-x: hidden;
  max-width: 100vw;
}
body * {
  font-family: var(--font-inter);
}

button {
  outline: none;
}

.container {
  max-width: 1782px;
  padding: 0 1.3125rem;
  position: relative;
}
@media (max-width: 768px) {
  .container {
    padding: 0 1rem;
  }
}
@media (max-width: 1780px) {
  .container {
    overflow: hidden;
  }
}

p {
  margin-bottom: 0;
  hyphens: auto;
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover {
  color: inherit;
}

ul {
  list-style-type: none;
  margin-bottom: 0;
}

h2.fs-2 {
  font-weight: 600;
  position: relative;
  z-index: 0;
}
@media (max-width: 992px) {
  h2.fs-2 {
    font-size: 2rem !important;
  }
}
@media (max-width: 550px) {
  h2.fs-2 {
    font-size: 1.3125rem !important;
  }
}

.hidden {
  overflow: hidden;
}

.main {
  padding-top: 7rem;
}
.main h1 {
  font-weight: 500;
  font-family: var(--font-unbounded);
}

.default-background {
  background: var(--white-background);
  border: 2px solid var(--white-background);
  backdrop-filter: blur(36px);
}

.default-list {
  list-style-type: disc;
}

.circle-1 {
  position: absolute;
  top: -49vw;
  right: 27.25vw;
  width: 100vw;
  min-height: 100vw;
  border-radius: 50%;
  background: radial-gradient(90% 90% at -6% 29%, rgba(210, 0, 140, 0) 0%, rgba(210, 0, 140, 0.3) 35%, #d20043 100%);
  transform: rotate(180deg);
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  mix-blend-mode: color-dodge;
  opacity: 0.5;
}
.circle-1:before {
  content: "";
  position: absolute;
  z-index: 0;
  top: 6%;
  left: -20%;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.75) 0%, rgba(255, 255, 255, 0.7) 15%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.08) 60%, rgba(255, 255, 255, 0) 66%);
  width: 55%;
  height: 55%;
  transform-origin: center;
}
@media (max-width: 992px) {
  .circle-1 {
    top: -8%;
    right: 38%;
    width: 100vh;
    min-height: 100vh;
  }
}

.circle-2 {
  position: absolute;
  top: 10vw;
  left: 65.6vw;
  width: 69.2vw;
  height: 69.2vw;
  border-radius: 50%;
  background: radial-gradient(90% 90% at -6% 29%, rgba(210, 0, 140, 0) 0%, rgba(210, 0, 140, 0.3) 35%, #d20043 100%);
  z-index: -1;
  pointer-events: none;
  overflow: hidden;
  mix-blend-mode: color-dodge;
  opacity: 0.5;
}
.circle-2:before {
  content: "";
  position: absolute;
  z-index: 0;
  top: -13%;
  left: -14%;
  background: radial-gradient(circle at center, rgba(255, 255, 255, 0.75) 0%, rgba(255, 255, 255, 0.7) 15%, rgba(255, 255, 255, 0.2) 50%, rgba(255, 255, 255, 0.08) 60%, rgba(255, 255, 255, 0) 66%);
  width: 55%;
  height: 55%;
}
@media (max-width: 992px) {
  .circle-2 {
    top: 29%;
    left: 60%;
    width: 69.2vh;
    height: 69.2vh;
  }
}
@media (max-width: 768px) {
  .circle-2 {
    left: 50%;
  }
}

.main-blur1 {
  position: absolute;
  top: -24vw;
  left: -3.5vw;
  transform: rotate(20deg);
  width: 100vw;
  height: 64vw;
  opacity: 0.4;
  z-index: -1;
  background: radial-gradient(50% 50% at 50% 50%, rgb(237, 24, 113) 0%, rgba(237, 24, 113, 0.5) 39.5%, rgba(237, 24, 113, 0.3) 70.99%, rgba(237, 24, 113, 0) 100%);
}
@media (max-width: 992px) {
  .main-blur1 {
    position: absolute;
    top: -46vw;
    left: -32.5vw;
    -webkit-transform: rotate(20deg);
    -ms-transform: rotate(20deg);
    transform: rotate(20deg);
    width: 150vw;
    height: 106vh;
    opacity: 0.5;
  }
}
@media (max-width: 768px) {
  .main-blur1 {
    width: 250vw;
    left: -68.5vw;
  }
}

.background-blur {
  pointer-events: none;
  background: radial-gradient(50% 50% at 50% 50%, rgba(190, 0, 104, 0.55) 0%, rgba(139, 10, 81, 0.5) 39.5%, rgba(71, 4, 41, 0.3) 70.99%, rgba(26, 1, 14, 0) 100%);
}

.background-blue-blur {
  pointer-events: none;
  background: radial-gradient(ellipse at center, rgba(70, 66, 243, 0.3) 0%, rgba(53, 47, 171, 0.2) 40%, rgba(38, 30, 106, 0.02) 71%, rgba(18, 7, 21, 0) 80%);
}
.background-blue-blur.services {
  width: 100%;
  min-height: 140%;
  opacity: 0.55;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 768px) {
  .background-blue-blur.services {
    width: 200%;
    height: 140%;
    opacity: 0.55;
    left: -50%;
    top: -10%;
  }
}
.background-blue-blur.helpful {
  width: 100%;
  height: 140%;
  opacity: 0.5;
  top: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 768px) {
  .background-blue-blur.helpful {
    opacity: 0.7;
    top: 20%;
    width: 200%;
  }
}
.background-blue-blur.reviews {
  width: 120%;
  height: 120%;
  opacity: 0.5;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (max-width: 768px) {
  .background-blue-blur.reviews {
    height: 160%;
  }
}

@media (max-width: 550px) {
  .container h2 {
    font-size: 1.3125rem !important;
  }
}

.accordion-wrapper {
  grid-template-columns: repeat(12, 1fr);
}
.accordion-wrapper > *:first-child {
  grid-column: 3/11;
}
@media (max-width: 1500px) {
  .accordion-wrapper > *:first-child {
    grid-column: 1/13;
  }
}